<template>
    <el-container id="app">
      <el-header>
        <img src="/static/img/icon-144.png">
          <span>MusicFM Operations Management System</span><!-- <br><span class="green" style="font-size: 16px;margin-left: 80px;margin-top: 5px;margin-bottom: 5px;"><a href="">warning<br>(, !)</a> --></span>
      </el-header>
      <el-container>
        <el-aside width="200px">
          <el-menu :default-active="activeIndex" class="el-menu-vertical-demo" :router="true" :unique-opened="true">
            <el-submenu index="songList">
              <template slot="title"><i class="el-icon-menu"></i></template>
              <el-menu-item index="/allList"></el-menu-item>
              <el-menu-item index="/HotSinger"></el-menu-item>
              <el-menu-item index="/ranking"></el-menu-item>
              <el-menu-item index="/recommend"></el-menu-item>
              <el-menu-item index="/latest"></el-menu-item>
              <el-menu-item index="/mfm_track"></el-menu-item>
              <el-menu-item index="/search_keywords"></el-menu-item>
            </el-submenu>
             <el-submenu index="publish">
              <template slot="title"><i class="el-icon-service"></i></template>
              <el-menu-item index="/app_info"></el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
</template>

<style lang="css">
@import "./styles/common.css";
@import "./styles/glow.css";
</style>

<script>
  export default {
    data() {
      return {
        activeIndex: '/allList'
      }
    },
    created: function() {
      console.log(this.$router.history.current.path)
      if(sessionStorage.getItem('username')) {
        this.activeIndex = this.$router.history.current.path
      }else {
        var hrefs = 'http://' + location.host + '/#/login';
        window.open(hrefs, '_self');
      }
    }
  }
</script>



